.leftnavsearch{
    background-color:  rgb(240, 240, 240);
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
}


.leftnavsearch .searchtab input{
    border: none;
    outline: none;
    background-color: white;
    color: black;
    font-size: 1rem;
    width: 30vw;
    height: 1rem;
    padding: 1.3rem 1rem; 
    border-radius: 50px;
}





/* navbar  */
.navbar i{
    color: black;
    font-size: 1.3rem;
    cursor: pointer;

}
.navbar{
    height: 10vh;
    padding: 0rem  1rem;
    background-color:  rgb(237,237,237);
    color: black;
}

.navbar,.navlist{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navlist div{
    margin-right: 1.5rem;
    
}


.logo img{
    width: 50px;
    border-radius: 50%;

}


.menulist{
    background-color: rgb(246, 246, 246);
    color:  black;
    width: 100%;
    height: 100%;
    padding: 1rem;
    border-radius: 4px;
    transition: 0.4s all ease-in;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.287);
    margin-top: 15rem;
}


.myprofile{
    height: 100%;
    width: 32vw;
    margin: 0rem 0rem;
    position: absolute;
    top: 0%;
    left: 0;
    background-color: rgb(246, 246, 246);
    transition: 0.4s all ease-in;
    
}
.myprofile .profiletextarea i{
    color: white;
    margin-right: 1rem;
    font-size: 2rem;
}
.myprofile .profiletextarea{
    padding: 1rem;
    height: 5rem;
    background-color: #00bfa5;
    color: white;
font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-right: 0 !important;
}

.namesection{
    padding: 1rem;
}
.myusername i{
    color: rgb(150, 150, 150);
}
.myusername p{
    color: rgb(90, 90, 90) !important;
    font-size: 1.2rem !important;
    
}

.myprofile .namesection{
margin-right: 0 !important;
background-color: white;


}
.myprofile .namesection .myusername{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.myprofile .namesection p , .biosection p{

    font-size: 400;
    font-size: 1.05rem;
    margin-bottom: 1rem;
    color:#00bfa5 ;
    
}
.myprofile .profileimg{
    background-color: rgb(240, 240, 240);
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 0  !important;
}

.myprofile .profileimg img{
    width: 200px;
    border-radius: 50%;
}


.normaltext{
    background-color: rgb(240, 240, 240);
    margin-right: 0 !important;
    padding: 1.5rem 3rem 1.5rem 1rem;
    color: rgb(85, 85, 85);
    font-size: .95rem;
}

.blanksection{
    background-color: rgb(240, 240, 240);
    height: 10rem;
    margin-right: 0 !important;
}


.biosection .myusername{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.biosection{
    padding: 1rem;
}