.whatsappbody{
    display:  flex;
    align-items: center;
    justify-content: center;
}

.leftside{
    width: 32vw;
    height: 100vh;
    border-right: 1px solid rgb(202, 202, 202);
}

.rightside{
    width: 68vw;
    height: 100vh;
}

::-webkit-scrollbar{
    width:8px;
    border-radius: 5px;
}

::-webkit-scrollbar-track{
    background: rgb(238, 238, 238);
    
}

::-webkit-scrollbar-thumb {
    background-color: rgb(209, 209, 209);
}