.chatTab{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 1rem;
    border-bottom: 1px solid rgb(224, 224, 224);
    cursor: pointer;
}
.chatTab:hover{
    background-color: rgb(248, 248, 248);
}

.ImgAndName{
    display: flex;
    align-items: center;
}

.chatTab .chatimg img{
    width: 50px;
    border-radius: 50%;
    margin: .6rem 1rem .6rem 0 ;


}

/* seen  */
.chatTab .nameDetails .uil-microphone{
    color: #34B7F1;

}

/* seen */

.chatTab .nameDetails .uil-microphone{

font-size: 2rem;
    font-weight: bolder;
}


.chatTab .nameDetails .uil{
    font-size: .8rem;
    margin-right: .4rem;

}
.chatTab .nameDetails .fas{
    font-size: .6rem;
    margin-right: .4rem;
    color: #34B7F1;
}
.chatTab .nameDetails p{
    font-weight: 400;
    font-size: .9rem;
    color: grey;
    margin-top: .3rem;
    overflow-y: hidden;
line-height: 1rem;
padding-bottom: .5rem;
}
.chatTab .nameDetails h4{
    font-weight: 600;
    font-size: 1.1rem;

}



.chatTab .timeandswitch{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
}

.chatTab .timeandswitch .mode i{
    color: grey;
}
.chatTab .timeandswitch .chatTime h5{
    color: grey;
font-weight: 400;
font-size: .7rem;

}
