@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}

body {
  overflow: hidden;
  height: 100vh;
  width: 100vw;

}

/* colors in whatsapp  

teal green = #128C7E
Teal Green Dark = #075E54
Light Green = #25D366
Blue = #34B7F1
 */

.whatsappbody{
    display:  flex;
    align-items: center;
    justify-content: center;
}

.leftside{
    width: 32vw;
    height: 100vh;
    border-right: 1px solid rgb(202, 202, 202);
}

.rightside{
    width: 68vw;
    height: 100vh;
}

::-webkit-scrollbar{
    width:8px;
    border-radius: 5px;
}

::-webkit-scrollbar-track{
    background: rgb(238, 238, 238);
    
}

::-webkit-scrollbar-thumb {
    background-color: rgb(209, 209, 209);
}
.leftnavsearch{
    background-color:  rgb(240, 240, 240);
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
}


.leftnavsearch .searchtab input{
    border: none;
    outline: none;
    background-color: white;
    color: black;
    font-size: 1rem;
    width: 30vw;
    height: 1rem;
    padding: 1.3rem 1rem; 
    border-radius: 50px;
}





/* navbar  */
.navbar i{
    color: black;
    font-size: 1.3rem;
    cursor: pointer;

}
.navbar{
    height: 10vh;
    padding: 0rem  1rem;
    background-color:  rgb(237,237,237);
    color: black;
}

.navbar,.navlist{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navlist div{
    margin-right: 1.5rem;
    
}


.logo img{
    width: 50px;
    border-radius: 50%;

}


.menulist{
    background-color: rgb(246, 246, 246);
    color:  black;
    width: 100%;
    height: 100%;
    padding: 1rem;
    border-radius: 4px;
    transition: 0.4s all ease-in;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.287);
    margin-top: 15rem;
}


.myprofile{
    height: 100%;
    width: 32vw;
    margin: 0rem 0rem;
    position: absolute;
    top: 0%;
    left: 0;
    background-color: rgb(246, 246, 246);
    transition: 0.4s all ease-in;
    
}
.myprofile .profiletextarea i{
    color: white;
    margin-right: 1rem;
    font-size: 2rem;
}
.myprofile .profiletextarea{
    padding: 1rem;
    height: 5rem;
    background-color: #00bfa5;
    color: white;
font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-right: 0 !important;
}

.namesection{
    padding: 1rem;
}
.myusername i{
    color: rgb(150, 150, 150);
}
.myusername p{
    color: rgb(90, 90, 90) !important;
    font-size: 1.2rem !important;
    
}

.myprofile .namesection{
margin-right: 0 !important;
background-color: white;


}
.myprofile .namesection .myusername{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.myprofile .namesection p , .biosection p{

    font-size: 400;
    font-size: 1.05rem;
    margin-bottom: 1rem;
    color:#00bfa5 ;
    
}
.myprofile .profileimg{
    background-color: rgb(240, 240, 240);
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 0  !important;
}

.myprofile .profileimg img{
    width: 200px;
    border-radius: 50%;
}


.normaltext{
    background-color: rgb(240, 240, 240);
    margin-right: 0 !important;
    padding: 1.5rem 3rem 1.5rem 1rem;
    color: rgb(85, 85, 85);
    font-size: .95rem;
}

.blanksection{
    background-color: rgb(240, 240, 240);
    height: 10rem;
    margin-right: 0 !important;
}


.biosection .myusername{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.biosection{
    padding: 1rem;
}
.chatTab{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 1rem;
    border-bottom: 1px solid rgb(224, 224, 224);
    cursor: pointer;
}
.chatTab:hover{
    background-color: rgb(248, 248, 248);
}

.ImgAndName{
    display: flex;
    align-items: center;
}

.chatTab .chatimg img{
    width: 50px;
    border-radius: 50%;
    margin: .6rem 1rem .6rem 0 ;


}

/* seen  */
.chatTab .nameDetails .uil-microphone{
    color: #34B7F1;

}

/* seen */

.chatTab .nameDetails .uil-microphone{

font-size: 2rem;
    font-weight: bolder;
}


.chatTab .nameDetails .uil{
    font-size: .8rem;
    margin-right: .4rem;

}
.chatTab .nameDetails .fas{
    font-size: .6rem;
    margin-right: .4rem;
    color: #34B7F1;
}
.chatTab .nameDetails p{
    font-weight: 400;
    font-size: .9rem;
    color: grey;
    margin-top: .3rem;
    overflow-y: hidden;
line-height: 1rem;
padding-bottom: .5rem;
}
.chatTab .nameDetails h4{
    font-weight: 600;
    font-size: 1.1rem;

}



.chatTab .timeandswitch{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
}

.chatTab .timeandswitch .mode i{
    color: grey;
}
.chatTab .timeandswitch .chatTime h5{
    color: grey;
font-weight: 400;
font-size: .7rem;

}

.chatarea{
    overflow-y:scroll ;
    height: 90vh;
}
.chatboxarea{
    background-image: url(/static/media/chatbg.e4f2569a.png);
    height: 100vh;
}
.chatfooterarea{
    position: fixed;
    bottom: 0;
    background-color: rgb(240,240,240);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 10vh;
    width: 68vw;
}


.chatfooterarea input{
    outline: none;
    height: 7vh;
    font-size: 1.1rem;
    border: none;
    padding: 0 2rem;
    width: 53vw;
    border-radius: 50px;
}

.chatfooterarea i{
    font-size: 2rem;
    color: rgb(100, 100, 100);
}
.chattingplayground{
    height: 80vh;
    overflow-y: scroll;
}
.messageentered{
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-right: 1.5rem;
    position: relative;
    bottom: 25%;
    height: 100vh;
}
.messageentered ul li{
width: 100%;
    background-color: #dcf8c6;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    font-size: 1rem;
    font-weight: 400;
    margin: 2rem 0;
    line-height: 20px;
    padding: 1rem;
}




.uil-message{
    cursor: pointer;
}


ul li .uil-check{
    color: grey;
    margin-left: .5rem;
    font-size: 1.5rem;
    position: relative;
    bottom: -5px;
}

.chattime{
    font-size: .8rem;
    color: grey;
    margin-left: .5rem;
}
.activeusernav{
    height: 10vh;
    padding: 0rem  1rem;
    background-color: rgb(237,237,237);
    color: black;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chatnavleft img{
    width: 50px;
    border-radius: 50%;
    margin-right: 1rem;
}
.chatnavleft h4{
    font-weight: 400;
    font-size: 1.2rem;
}
.chatnavleft,.chatnavright {
    display: flex;
    align-items: center;
}
.chatnavright div{
    
    margin-right: 2rem;
}

.chatnavright .chatsearch{
    border-left: 1px solid black;
    padding-left: 2rem;
}

.chatnavright div i{
    cursor: pointer;
    font-size: 1.3rem;
}


