.chatfooterarea{
    position: fixed;
    bottom: 0;
    background-color: rgb(240,240,240);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 10vh;
    width: 68vw;
}


.chatfooterarea input{
    outline: none;
    height: 7vh;
    font-size: 1.1rem;
    border: none;
    padding: 0 2rem;
    width: 53vw;
    border-radius: 50px;
}

.chatfooterarea i{
    font-size: 2rem;
    color: rgb(100, 100, 100);
}