.chattingplayground{
    height: 80vh;
    overflow-y: scroll;
}
.messageentered{
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-right: 1.5rem;
    position: relative;
    bottom: 25%;
    height: 100vh;
}
.messageentered ul li{
width: 100%;
    background-color: #dcf8c6;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    font-size: 1rem;
    font-weight: 400;
    margin: 2rem 0;
    line-height: 20px;
    padding: 1rem;
}




.uil-message{
    cursor: pointer;
}


ul li .uil-check{
    color: grey;
    margin-left: .5rem;
    font-size: 1.5rem;
    position: relative;
    bottom: -5px;
}

.chattime{
    font-size: .8rem;
    color: grey;
    margin-left: .5rem;
}