.activeusernav{
    height: 10vh;
    padding: 0rem  1rem;
    background-color: rgb(237,237,237);
    color: black;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chatnavleft img{
    width: 50px;
    border-radius: 50%;
    margin-right: 1rem;
}
.chatnavleft h4{
    font-weight: 400;
    font-size: 1.2rem;
}
.chatnavleft,.chatnavright {
    display: flex;
    align-items: center;
}
.chatnavright div{
    
    margin-right: 2rem;
}

.chatnavright .chatsearch{
    border-left: 1px solid black;
    padding-left: 2rem;
}

.chatnavright div i{
    cursor: pointer;
    font-size: 1.3rem;
}

